import { SidebarCollapse, SidebarExpand } from "@rp/react/common/assets"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Box, IconButton, Stack } from "@rp/react/web/mui/core"

type Props = {
  toggleDrawer: () => void
  expanded: boolean
  menuComponent: React.ReactNode
  trialCTAComponent?: React.ReactNode
  freeTierCTAComponent?: React.ReactNode
}

export const SidebarFooter = ({
  toggleDrawer,
  expanded,
  menuComponent,
  trialCTAComponent,
  freeTierCTAComponent,
}: Props) => {
  return (
    <Box
      sx={{
        width: "100%",
        position: "sticky",
        bottom: 0,
        zIndex: 50,
        backgroundColor: "surface.main",
      }}
    >
      <Stack direction="column" gap={2} width="100%" px={1.5} mb={1.5}>
        {trialCTAComponent}
        {freeTierCTAComponent}
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            flexDirection: expanded ? "row" : "column-reverse",
            gap: 1,
          }}
        >
          {menuComponent}
          <RPTooltip
            key={expanded ? "1" : "2"}
            title={expanded ? "Collapse menu" : "Expand menu"}
            placement="right"
          >
            <IconButton
              disableRipple
              onClick={toggleDrawer}
              sx={{
                ml: expanded ? 0.5 : 0,
                cursor: "pointer",
                width: 28,
                height: 28,
                p: 0.5,
                borderRadius: 1,
                backgroundColor: "elevation.dp08",
                "&:hover": {
                  backgroundColor: "elevation.dp24",
                },
              }}
            >
              {expanded ? <SidebarCollapse /> : <SidebarExpand />}
            </IconButton>
          </RPTooltip>
        </Box>
      </Stack>
    </Box>
  )
}
